*,
*::after,
*::before {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

body {
  margin: 0;
}

@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.page {
  height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  color: #000000;
}

.container {
  padding: 0 30px;
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}

.button {
  font-family: "OpenSans", "Arial", sans-serif;
  display: block;
  border: 2px solid #444444;
  cursor: pointer;
  background-color: transparent;
  text-align: center;
  font-size: 18px;
  line-height: 20px;
  width: 100%;
  padding: 14px 10px;
  text-transform: uppercase;
  color: #444444;
  transition: 0.5s;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active, .button:focus-visible {
  color: rgba(255, 255, 255, 0.3);
  outline: none;
  background-color: #444444;
}
.button:disabled {
  background-color: rgba(68, 68, 68, 0.3);
  color: rgba(255, 255, 255, 0.3);
  pointer-events: none;
}
.button[aria-disabled=true] {
  opacity: 0.5;
  pointer-events: none;
}
.button--second {
  font-family: "OpenSans", "Arial", sans-serif;
  display: block;
  border: 2px solid #444444;
  cursor: pointer;
  background-color: transparent;
  text-align: center;
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  padding: 17px 10px;
  text-transform: uppercase;
  width: 100%;
  transition: 0.5s;
}
@media (min-width: 768px) {
  .button--second {
    padding: 16px 10px;
  }
}
.button--second:hover {
  background-color: #444444;
  color: #ffffff;
}
.button--second:active, .button--second:focus-visible {
  color: rgba(255, 255, 255, 0.3);
  outline: none;
  background-color: #444444;
}
.button--second:disabled {
  background-color: rgba(68, 68, 68, 0.3);
  color: rgba(255, 255, 255, 0.3);
  pointer-events: none;
}
.button--accent {
  font-size: 17px;
  line-height: 16px;
  color: #ffffff;
  background-color: #63d1bb;
  padding: 18px 10px;
  border: none;
}
@media (min-width: 768px) {
  .button--accent {
    max-width: 350px;
  }
}
.button--accent:hover {
  background-color: #56c3b2;
}
.button--accent:active, .button--accent:focus-visible {
  color: white;
  outline: none;
  background-color: rgba(86, 195, 178, 0.5);
}

.input {
  padding: 17px 17px 16px 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 400;
  line-height: 18px;
  color: rgba(0, 0, 0, 0.3);
  border: none;
  width: 100%;
  transition: 0.3s;
  box-shadow: inset 0 -2px 0 0 #000000;
  background-color: transparent;
}
@media (min-width: 768px) {
  .input {
    margin: 0 0 0 24px;
    padding: 13px 17px 13px;
    font-size: 19px;
  }
}
.input:hover, .input:active, .input:focus {
  box-shadow: inset 0 -2px 0 0 #62d1ba;
  outline: none;
}
.input:active ~ .form__icon, .input:focus ~ .form__icon {
  color: #62d1ba;
  opacity: 1;
}
.input:disabled {
  box-shadow: inset 0 -2px 0 0 rgba(0, 0, 0, 0.3);
  pointer-events: none;
}
.input:disabled::placeholder {
  color: rgba(0, 0, 0, 0.2);
}
.input--phone {
  padding: 18px 17px 20px 0;
}
@media (min-width: 768px) {
  .input--phone {
    margin: 0;
    padding: 12px 17px 16px 17px;
  }
}
.input--email {
  padding: 20px 17px 17px 0;
}
@media (min-width: 768px) {
  .input--email {
    margin: 0;
    padding: 12px 17px 16px 17px;
  }
}

.input:not(:placeholder-shown) {
  color: #444444;
}

.input::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.title {
  font-size: 27px;
  font-weight: 700;
  line-height: 30px;
  color: #000000;
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}
.title--second {
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .title--second {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title--second {
    font-size: 53px;
    line-height: 57px;
  }
}

.main-container {
  flex-grow: 1;
}

.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 71px 0 0;
  margin: 0 0 31px;
  background-color: #f9f9f9;
  min-height: 488px;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .hero {
    min-height: 678px;
    padding: 88px 0 0;
    margin: 0 0 52px;
  }
}
@media (min-width: 1150px) {
  .hero {
    padding: 90px 0 0;
    margin: 0 0 71px;
  }
}

.hero::before {
  content: "";
  width: 100%;
  height: 373px;
  top: -20px;
  left: 0;
  position: absolute;
  background-image: url("../img/hero/hero-mobile.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-resolution: 2dppx) {
  .hero::before {
    background-image: url("../img/hero/hero-mobile@2x.png");
  }
}
@media (min-width: 768px) {
  .hero::before {
    display: block;
    height: 746px;
    background-image: url("../img/hero/hero-tablet.png");
    background-position: center bottom;
    background-size: contain;
    top: -69px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero::before {
    background-image: url("../img/hero/hero-tablet@2x.png");
  }
}
@media (min-width: 1150px) {
  .hero::before {
    display: block;
    height: 746px;
    background-image: url("../img/hero/hero-desktop.png");
    background-position: center bottom;
    background-size: contain;
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .hero::before {
    background-image: url("../img/hero/hero-desktop@2x.png");
  }
}

.hero::after {
  position: absolute;
  content: "";
  left: 0;
}
@media (min-width: 768px) {
  .hero::after {
    background-image: url("../img/zigzagwhite-tablet.svg");
    z-index: 3;
    width: 100%;
    height: 27px;
    bottom: -14px;
    background-repeat: repeat-x;
  }
}
@media (min-width: 1150px) {
  .hero::after {
    background-image: url("../img/zigzagwhite-desktop.svg");
    z-index: 3;
    width: 100%;
    height: 35px;
    bottom: -18px;
  }
}

.hero__title {
  margin: 0 30px 0;
  position: relative;
  z-index: 5;
}
@media (min-width: 768px) {
  .hero__title {
    margin: 0 auto 0;
    max-width: 700px;
    text-align: center;
    position: relative;
    z-index: 5;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    margin: 0 auto 0;
  }
}

.hero__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
}
@media (min-width: 768px) {
  .hero__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0 50px;
  }
}
@media (min-width: 1150px) {
  .hero__list {
    width: 1050px;
    margin: 0 auto;
  }
}
.hero__item {
  display: flex;
  background-color: #63d1bb;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .hero__item {
    margin: 0;
    min-height: 119px;
  }
}
.hero__item:hover {
  background-color: #59d5bc;
  transform: translateY(-5px);
}
.hero__item--toy {
  background-color: #56c3b2;
}
.hero__item--toy:hover {
  background-color: #46c1ae;
}

.hero__link {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 23px 36px 18px 30px;
  align-items: center;
}
.hero__link:hover .hero__link-icon {
  opacity: 0.7;
}
.hero__link:active .hero__link-icon {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .hero__link {
    font-size: 20px;
    line-height: 30px;
    padding: 20px 36px 18px 35px;
  }
}
@media (min-width: 1150px) {
  .hero__link {
    padding: 22px 36px 18px 37px;
  }
}
.hero__link--toy {
  padding: 23px 27px 20px 30px;
}
@media (min-width: 768px) {
  .hero__link--toy {
    padding: 24px 33px 22px 36px;
  }
}
@media (min-width: 1150px) {
  .hero__link--toy {
    padding: 27px 33px 22px 36px;
  }
}

.hero__link-text {
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  max-width: 140px;
  margin: -11px 0 0 0;
}
@media (min-width: 768px) {
  .hero__link-text {
    font-size: 20px;
    line-height: 30px;
  }
}

.hero__link-icon {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .hero__link-icon {
    margin: 5px 0 0 0;
  }
}
@media (min-width: 1150px) {
  .hero__link-icon {
    margin: 0;
  }
}

.main-nav__wrapper {
  position: relative;
  display: flex;
  box-shadow: 0 1px 0 0 #ececec, inset 0 1px 0 0 #ececec;
  z-index: 4;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    margin: 0;
    z-index: 6;
  }
}

.main-nav__logo {
  display: flex;
  margin: 0 auto;
  padding: 21px 0;
}
@media (min-width: 768px) {
  .main-nav__logo {
    position: absolute;
    padding: 22px 0 0 50px;
    margin: 0 0 0 -3px;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo {
    left: 50%;
    top: 50%;
    transform: translate(-49%, 12%);
    padding: 0;
  }
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__logo:active, .main-nav__logo:focus-visible {
  opacity: 0.3;
  outline: none;
}

.main-nav__logo-image {
  width: auto;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list,
.main-nav--closed .main-nav__user {
    display: none;
    flex-direction: column;
    padding: 0;
    margin: 0;
    list-style-type: none;
  }
}

.main-nav--open .main-nav__toggle-line:first-child {
  top: 6px;
  transform: rotate(45deg);
}

.main-nav--open .main-nav__toggle-line:nth-child(2) {
  transform: rotate(-45deg);
}

.main-nav--open .main-nav__toggle-line:nth-child(3) {
  bottom: 6px;
  transform: scale(0);
}

.main-nav__toggle {
  display: block;
  position: absolute;
  top: 32px;
  right: 31px;
  width: 20px;
  height: 14px;
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:hover {
  opacity: 0.5;
}
.main-nav__toggle:active, .main-nav__toggle:focus-visible {
  opacity: 0.3;
  outline: none;
}

.main-nav__toggle-line {
  position: absolute;
  width: 20px;
  height: 2px;
  left: 0;
  right: 0;
  display: block;
  background-color: #231f20;
  transition: 0.3s;
}

.main-nav__toggle-line:first-child {
  top: 0;
}

.main-nav__toggle-line:nth-child(2) {
  top: 6px;
}

.main-nav__toggle-line:nth-child(3) {
  bottom: 0;
}

@media (max-width: 767px) {
  .main-nav--open .main-nav__container {
    position: absolute;
    width: 100%;
    z-index: 6;
    background-color: #ffffff;
  }
}

@media (min-width: 768px) {
  .main-nav__container {
    display: grid;
    grid-template-columns: 50px 1fr 2fr 2fr 50px;
    grid-template-rows: auto auto;
  }
}
@media (min-width: 1150px) {
  .main-nav__container {
    display: grid;
    grid-template-columns: 1fr 432px 183px 432px 1fr;
    grid-template-rows: auto auto;
  }
}

@media (min-width: 768px) {
  .main-nav__background {
    grid-row: 2/-1;
    grid-column: 1/-1;
    background-color: #f9f9f9;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    z-index: 5;
  }
}
.main-nav__background--accent {
  background-color: #f1f1f1;
}

.main-nav__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    display: flex;
    flex-wrap: wrap;
    z-index: 5;
    grid-row: 2/-1;
    grid-column: 2/4;
  }
}
@media (min-width: 1150px) {
  .main-nav__list {
    grid-row: 1/2;
  }
}

@media (min-width: 768px) {
  .main-nav__item {
    max-width: 44%;
  }
}
@media (min-width: 1150px) {
  .main-nav__item {
    max-width: 38%;
  }
}

.main-nav__list .main-nav__link {
  font-size: 17px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .main-nav__list .main-nav__link {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .main-nav__list .main-nav__link {
    font-size: 17px;
    padding: 33px 36px 31px 0;
    height: 100%;
  }
}
@media (min-width: 1150px) {
  .main-nav__list .main-nav__link--border {
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
}

@media (min-width: 768px) {
  .main-nav__list .main-nav__item:not(:last-child) {
    margin: 0 23px 0 0;
  }
}
@media (min-width: 1150px) {
  .main-nav__list .main-nav__item:not(:last-child) {
    margin: 0 35px 0 0;
  }
}

.main-nav__user {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .main-nav__user {
    flex-direction: row;
    display: flex;
    flex-wrap: wrap;
    grid-column: 3/5;
    grid-row: 1/2;
    justify-content: flex-end;
  }
}
@media (min-width: 1150px) {
  .main-nav__user {
    grid-column: 4/5;
    grid-row: 1/2;
  }
}

@media (max-width: 767px) {
  .main-nav__item,
.main-nav__user-item {
