.products {
  position: relative;
  padding: 0 0 42px;
}
@media (min-width: 768px) {
  .products {
    padding: 0 0 66px;
  }
}
@media (min-width: 1150px) {
  .products {
    padding: 0;
  }
}

.products::before {
  content: "";
  position: absolute;
  background-image: url("../img/zigzag-line-mobile.svg");
  bottom: 0;
  width: 100%;
  height: 6px;
  left: 0;
}
@media (min-width: 768px) {
  .products::before {
    background-image: url("../img/zigzag-line-tablet.svg");
    bottom: 0;
    width: 100%;
    height: 17px;
    left: 0;
  }
