.products__top-wrapper {
  margin: 0 0 17px;
}
@media (min-width: 768px) {
  .products__top-wrapper {
    display: grid;
    grid-template-columns: 1fr 120px;
    align-items: center;
    margin: 0 0 38px;
    grid-column: 1/-1;
    grid-row: 1/2;
  }
}
@media (min-width: 1150px) {
  .products__top-wrapper {
    display: grid;
    grid-template-columns: 120px 1fr;
    column-gap: 33px;
